.App {
  box-sizing: border-box;
  font-family: 'Poppins';
}

nav{
  height: 70px;
  display: flex;
  padding : 20px 36px;
  background: #FFFFFF;
box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.1);
}

section{
padding: 20px;  
}
.hero{
  display: flex;
  flex-direction: column;
}
.hero--img{
  max-width: 500px;
  align-self: center;
}
 .hero--title{
  margin-bottom: 16px;
 }
 .hero--text{
  margin-top: 0px;
 }
 .card{
  position: relative;
  width: 175px;
  font-size: 14px;
  flex: 0 0 auto;
  display: flex;
  flex-direction: column;
 }
 .card-name{
  overflow: hidden;
  text-overflow: ellipsis;
 }
 .card--image{
  max-height: 235px;
 }
 .card--stats{
  display: flex;
  align-items: center;
 }
 .bold{
  font-weight: bold;
 }
.grey{
  color: #918E9B;
}
.card--country{
  /* text-decoration-color: darkgray; */
  font-style: normal;
font-weight: 300;
font-size: 12px;
/* line-height: 110%; */
color: #918E9B;
}
.card--star{
  width: 14px;
height: 14px;
}
.cards-list{
display: flex;
flex-direction: row;
}
.card--badge{
  position: absolute;
  margin-top: 6px;
  margin-left: 6px;
  background-color: white;
  padding: 5px 7px;
  border-radius: 2px;
  font-weight: bold;
}